import React from 'react'
import Progress from 'react-progressbar';
import Grid from '@material-ui/core/Grid';
import './ProgressBars.css'
function ProgressBars(props) {
    return (
        <div>
            <Grid container className="holder" >
                <Grid item lg={2} xs={0} ></Grid>
                <Grid item lg={1} xs={12}  style={{textAlign:"center"}}   className="text" >{props.language}</Grid>
                <Grid item lg={7} xs={12} style={{margin:"0 2em 0 3em"}} >
                    
                <Progress completed={props.precentage} color="#424874"  className="progress" height="1.6em"  />
                </Grid>
                <Grid item xs={0} lg={2}></Grid>
            </Grid>
        </div>
    )
}

export default ProgressBars
